.featured-work{
  background-color: #fff;
  text-align: center;
  padding-top: 80px;
  &__title{
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 20px;
    font-weight: 400;
    letter-spacing: 3px;
    color: #333b46;
  }
  &__text{
    margin: 40px auto 0;
    max-width: 470px;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    color:#81868e ;
  }
  &__card{
    display: flex;
    .card-box{
      width: 25%;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover{
        background-color: rgba(0,0,0,.5);
      }
      &:first-child{
        width: 50%;
      }
      &__text{
        font-family: 'Source Sans Pro', sans-serif;
        font-size: 20px;
        font-weight: 400;
        letter-spacing: 3px;
        color: #fff;
        text-transform: uppercase;
      }
    }
  }
}